<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>食品管理</el-breadcrumb-item>
      <el-breadcrumb-item>食品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="30">
        <el-col :span="6">
          <el-input v-model="queryInfo.query" placeholder="请输入食品名称" class="input-serch"></el-input>
        </el-col>
        <el-col :span="3">
            <el-button type="primary" icon="el-icon-search" @click="searchGoods">搜索</el-button>
        </el-col>
        <el-col :span="4">
            <el-button type="primary" icon="el-icon-plus" @click="toAdd">添加</el-button>
        </el-col>
        <!-- <el-col :span="16">
          <el-button type="primary" @click="addShop">添加</el-button>
        </el-col> -->
      </el-row>
      <!-- 列表形式 -->
      <el-table v-loading="loading" :data="foods" stripe border style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="fname" label="食品名称" align="center"></el-table-column>
        <el-table-column prop="fprice" label="出售价格" align="center">
           <template slot-scope="scope">
            <div>￥{{scope.row.fprice.toFixed(2) }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="ftime" label="上架时间" align="center">
           <template slot-scope="scope">
            <div>{{scope.row.ftime | timeflow }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="tid" label="食品类型" align="center">
          <template slot-scope="scope">
            <div v-if="scope.row.tid==1">热门菜品</div>
            <div v-if="scope.row.tid==2">特色菜品</div>
            <div v-if="scope.row.tid==3">精品推荐</div>
            <div v-if="scope.row.tid==4">香辣菜品</div>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button @click="toEdit(scope.row)" type="primary" icon="el-icon-edit" circle></el-button>
            <el-button @click="foodDelete(scope.row.fid)" type="danger" icon="el-icon-delete" circle ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页底部 -->
       <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageIndex"
        :page-sizes="[5,10]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 修改商品的对话框 -->
    <el-dialog
      title="修改商品"
      :visible.sync="editShow"
      width="50%"
      center
      @close="editClose"
    >
      <!-- 内容主题区域 -->
      <el-form :model="editFood" :rules="editRules" ref="editRules" label-width="150px">
        <el-form-item label="食品名称" prop="fname">
          <el-input v-model="editFood.fname"></el-input>
        </el-form-item>
        <el-form-item label="出售价格" prop="fprice">
          <el-input type="number" v-model="editFood.fprice"></el-input>
        </el-form-item>
        <el-form-item label="食品类型" prop="tid">
          <el-select v-model="editFood.tid" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.tid"
              :label="item.tname"
              :value="item.tid">
            </el-option>
          </el-select>
        </el-form-item>
         <el-form-item label="食品描述" prop="fdesc">
            <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4}"
            placeholder="请输入内容"
            v-model="editFood.fdesc">
          </el-input>
        </el-form-item>
      </el-form>

      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editClose('editRules')">取 消</el-button>
        <el-button type="primary" @click="editCommit('editRules')">确认修改</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import axios from 'axios'
import Vue from 'vue'

//过滤器，对时间格式进行转换
Vue.filter('timeflow',date=>{
  if(!date) return ''
  const da = new Date(date);
  let time = da.getFullYear();
  const month = da.getMonth()+1;
  const day = da.getDate();
  //针对月份，天进行拼接处理
  if(month<10)time=time+'-0'+month;
  else time = time+'-'+month;
  if(day<10)time=time+'-0'+day;
  else time = time+'-'+day;
  return time;
})
export default {
  data() {
     var valiNumberPass1 = (rule, value, callback) => {//正整数
            if (value === '') {
                callback(new Error('请输入出售价格价格'));
            } else if (isNaN(parseFloat(value))) {
                callback(new Error('请输正确的数值'));
            } else {
                callback();
            }
     };
    return {
      loading:true,
      searchValue:'',
      foods:[],
      options:[],
      editFood:{
        fid:'',
        fname:'',
        fprice:'',
        fdesc:'',
        tid:'',
      },
      editShow:false,
      queryInfo:{
        query:'',
        pageIndex:1,
        pageSize:5
      },
      total:0,
      editRules:{
        fname:[{ required: true, message: '请输入食品名称', trigger: 'blur' }],
        fprice:[{ required: true, validator:valiNumberPass1, trigger: "blur" }],
        fdesc:[{ required: true, message: '请输入食品描述', trigger: 'blur'}],
      }
      
    }
  },
  methods: {
    searchGoods(){
      this.getFoods();
    },
    toEdit(val){
      //拷贝
      this.editFood.fid = val.fid;
      this.editFood.fname = val.fname;
      this.editFood.fprice = val.fprice;
      this.editFood.fdesc = val.fdesc;
      this.editFood.tid = val.tid;
      //请求获取食品类型
      this.getFoodType();
      this.editShow = true;
    },
    editClose(){
      this.editShow = false;
      //重置规则
      // this.$refs[formName].resetFields(); 
     //会报错
     //原因是：mouted加载数据以后，隐藏的弹出框并没有编译渲染进dom里面
     //。所以click弹出的时候$refs并没有获取到dom元素导致 ‘resetFields’ of undefined
      this.$nextTick(()=>{
        this.$refs.editRules.resetFields();
      });
    },
    toAdd(){
      this.$router.push('/addfood');
    },
    checkGname(){
      this.editFood.fname
    },
    editCommit(formName){
      this.$refs[formName].validate(valid => {
        if(valid){
          //请求
          axios.post('/api/dincan/admin/updateFoodBase',this.editFood).then(res=>{
            if(res.data==1){
              this.$message.success('修改成功！');
              this.getFoods();
            }else{
              this.$message.error('修改失败！');
            }
            this.editShow = false;
          })
        }else{
          return false;
        }
      })

      
    },
    handleSizeChange(val){
      this.queryInfo.pageSize = val;
      this.getFoods();
    },
    handleCurrentChange(val){
      this.queryInfo.pageIndex = val;
      this.getFoods();
    },
    //根据id删除食品
   async foodDelete(fid){
      const re = await this.$confirm('此操作将永久删除该食品信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
      }).catch(err=>err)
      if(re !='confirm'){
        this.$message.info('已取消删除！')
      }else{
        axios.get("api/dincan/admin/deleteFood?fid="+fid).then(res=>{
          if(res.data==1){
            this.$message.success('删除成功！')
            this.getFoods();
          }else{
            this.$message.error('删除失败！')
          }
        })
      }
    },
    //获取所有食品类型
    getFoodType(){
      axios.get('/api/dincan/admin/getTypes').then(res=>{  
          this.options = res.data;
      })
    },
    //查询食品信息
    getFoods(){
      axios.post("api/dincan/admin/getFoods",this.queryInfo).then(res=>{
            this.loading = false;
            this.foods = res.data.list;
            this.total = res.data.count;
      })
    },
  },
  mounted() {
    //获取商品数据
    this.getFoods();
  },
}
</script>
<style scoped>
/* .input-serch{
  width: 130px;
} */
</style>